.form-container{
    margin: 0;
    padding: 0 0 5% 0;    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;    
}

.form-container ul{
    margin: 0;
    padding: 3%;
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    /* border: 1px solid blue; */
}
.form-container ul li{
    margin: 0 0 2%;
    padding: 0 1%;
    width: 31%;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    font-size: 14px;
}
.form-container ul li label{
    margin: 0;
    padding: 0;
    align-self: flex-start;
}
.form-container ul li input{
    margin: 0;
    padding: 0 2%;
    height: 35px;
    background-color: #f2f2f2;
    border-radius: 5px;
    border: 1px solid #cccccc;
}
.form-container ul li select{
    margin: 0;
    padding: 0 2%;
    height: 35px;
    background-color: #f2f2f2;
    border-radius: 5px;
    border: 1px solid #cccccc;
}

.form-button{
    background-color: #04AA6D; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

@media print{
    .form-container{
        display: none;
    }
}

@media only screen and (max-width: 600px){
    .form-container ul li {
        width: 100%;
    }
}