@media screen{
    
.donationreceipt-container{
    display:none;
}
}

@media print{

    .donationreceipt-container{
        max-width:100vw;
        display: flex;
        justify-content: center;
    }   
    
    .donation-receipt{
        margin:0%;
        padding:1%;
        width:95%;
        border: 2px dashed #29b9f7;
        box-shadow: inset 0 0 0 1000px #d4f2ff;
    }
    
    .donationreceipt-header{
        /* border: 1px solid blue; */
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .donationreceipt-header img{
        width:100%;        
    }
    
    .donationreceipt-header .letter-head{
        margin:0;
        padding: 0;    
        /* border: 1px solid orange; */
    }
    


    /* DONATION CONTENT */
    
    .donationreceipt-content{
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    .donationreceipt-content h2{
        font-size: 20px;
    }
    
    .donationreceipt-content ul{
        margin: 0;
        padding: 0;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        /* border: 1px solid orange; */
    }
    .donationreceipt-content ul li{
        margin: 0 2% 2% 0;
        padding: 0;
        list-style-type: none;
        width: 46%;
        font-family: 'Courier New', Courier, monospace;
        color: #202020;
        font-weight: 600;
        border-bottom: 1px dashed #979797;
        text-align: left;
    }
    
    .receipt-thankyou-message{
        font-family: 'Roboto',sans-serif;
    }
    .receipt-note{
        font-size: 16px;
        color: #000000;
        font-weight: bold;
        text-transform: uppercase;
    }
    .donationreceipt-content ul li span{
        font-family: 'Roboto',sans-serif;
        font-size: 14px;
        font-weight: normal;
        color: #979797;
    }
    .donationreceipt-signature-stamp-container{
        margin: 0%;
        padding: 8% 0% 3% 0%;
        width: 100%;
        font-weight: 500;
        display: flex;
        justify-content: space-between;
        /* border: 1px solid orange; */
    }
    .donar-photo{
        margin: 0 0 5%;
        padding: 0;
        width: 18%;
        background-color: beige;
        border: 1px solid #cccccc;
    }
    
    .donationreceipt-amount-in-digits{
        display: flex;
        flex-direction: column;
        /* border: 1px solid blue; */
    }
    .donationreceipt-amount-in-digits span{
        border: 1px solid #202020;
        border-radius: 10px;
        padding: 2% 5%;
        margin: 0%;
    }

    .donationreceipt-stamp{
        display: flex;
        flex-direction: column;    
        justify-content: center;
        width: 33%;
        background-size: contain;
        background-repeat: no-repeat;
        background-position-x: center;
    }
    .donationreceipt-stamp img{
        width: 40%;
        align-self: center;
    }
    .donationreceipt-stamp p{
        position: absolute;
        z-index: 9;
    }

    .donationreceipt-signature{
        display: flex;
        flex-direction: column;    
        justify-content: center;
        width: 33%;
        background-size: contain;
        background-repeat: no-repeat;
        background-position-x: center;
        /* border: 1px solid orange;     */
    }
    .donationreceipt-signature img{
        width: 40%;
        align-self: center;
    }
    .donationreceipt-signature p{
        margin: 0;
    }
 
}