.footer-container{
    display: flex;
    flex-direction: column;
    background-color: #faf6e8;
    margin: 0%;
    padding: 0.8% 0;
}
.footer-container h5{
    margin: 0%;
    padding: 0%;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: normal;
    color:#090802;    
}
@media print{
    .footer-container{
        display:none;
    }
}